export function getSearchParametersFromHRef(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("?");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}

export function getSearchParametersFromHash(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("#");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}
function auth() {
  return new Promise((resolve, reject) => {
    try {
      var replyUrl =
        window.location.protocol +
        "//" +
        window.location.hostname +
        (window.location.port !== 80 &&
        window.location.port !== 443 &&
        window.location.port !== ""
          ? ":" + window.location.port
          : "") +
        window.location.pathname;

      var msalConfig = {
        auth: {
          clientId: "443ae28d-8cf8-42fd-ba63-f403ac085ead",
          redirectUri: replyUrl
        }
      };
      var requestObj = {
        scopes: ["user.read"]
      };
      var msalInstance = new UserAgentApplication(msalConfig);
      msalInstance.handleRedirectCallback((error, response) => {
        // handle redirect response or error
      });
      var PTO365 = {
        user: msalInstance.getAccount()
      };
      if (!PTO365.user) {
        msalInstance.loginRedirect(requestObj);
      }

      resolve(PTO365.user);
    } catch (error) {
      reject(error);
    }
  });
}
